
import { defineComponent } from "vue";
import { mapState, mapGetters } from "vuex";
import { Notify } from "vant";

export default defineComponent({
  name: "GoBack",
  data() {
    return {};
  },
  setup: () => {
    return {};
  },
  computed: {},
  methods: {
    back() {
      try {
        this.$router.go(-1);
      } catch (err) {
        Notify({ type: "danger", message: "返回失败" });
      }
    },
  },
});
