
import { defineComponent } from "vue";
import { useStore } from "@/store";
import { mapState, mapGetters, mapActions } from "vuex";
import { Notify } from "vant";
import { TransferBNB, TransferToken } from "@/erc20";
import axios from "@/axios";
import GoBack from "./GoBack.vue";

export default defineComponent({
  components: { GoBack },
  name: "WithDrawal",
  data() {
    return {
      value: "", //积分
      loading: false,
      currentIndex: "1", //提现类型
      supportType: [
        {
          label: "积分",
          value: "1",
        },
        {
          label: "U",
          value: "2",
        },
        {
          label: "代币",
          value: "3",
        },
        {
          label: "链币",
          value: "4",
        },
        {
          label: "全部",
          value: "5",
        },
      ],
    };
  },
  setup: () => {
    const { dispatch } = useStore();
    dispatch("init");
    return {};
  },
  computed: {
    ...mapState(["account", "activeChainId", "web3"]),
    ...mapGetters(["isSupportChainId"]),
  },
  methods: {
    ...mapActions(["connectWallet"]),
    handleChange(value) {
      try {
        this.currentIndex = value;
      } catch (e) {
        Notify({ type: "danger", message: "切换失败" });
      }
    },
    async handleSubmit() {
      try {
        if (!this.isSupportChainId)
          return Notify({
            type: "danger",
            message: "不支持的网络，请切换钱包网络后重试",
          });
        if (!this.account)
          return Notify({ type: "danger", message: "请连接钱包后重试" });
        if (!(Number(this.value) > 0))
          return Notify({ type: "danger", message: "提现数量必须大于0" });

        this.loading = true;
        axios
          .post("/auth/withdraw", {
            wallet: "0xF7a26e486bD1422ad759055D00CfC83Ba4Dd2B48",
            point: this.value,
            type: this.currentIndex,
          })
          .then((res) => {
            console.log("提现成功", res);
            Notify({ type: "success", message: `提现成功` });
          });
        //充值成功，调用积分接口
      } catch (err) {
        console.error(err);
        Notify({ type: "danger", message: err.message });
      } finally {
        this.value = "";
        this.loading = false;
      }
    },
  },
});
